// import {print} from './print' // 这样渲染index.js同时会渲染print.js

document.getElementById('btn').onclick = function() {
    // webpack懒加载  文件不会被加载 只有触发才会加载
    // import('./print')
    // .then(({print}) => {
    //     print()
    // })
    // .catch((err) => {
    //     console.log(err)
    // })
    
    // 预加载  文件会预先加载 但不会触发结果
    import(/*webpackPrefetch: true*/'./print')
    .then(({print}) => {
        print()
    })
    .catch((err) => {
        console.log(err)
    })
}